.topbarContainer{
    height:50px;
    width:100%;
    background-color: rgba(215, 88, 14, 0.841);
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
}

.topbarLeft{
    flex: 3;
}

.topbarCenter{
    flex: 5;
}

.searchbar{
width: 100%;
height: 30px;
background-color: white;
border-radius: 30px;
display:flex;
align-items:center;
}

.searchicon{
    font-size: 25px !important;
    margin-left:10px;
}

.searchInput{
    border: none;
    width: 70%;
}

.searchInput:focus{
    outline: none;
}



.topbarRight{
    flex: 4;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.topbarLink{
    margin-right: 10px;
    font-size: 14px;
    cursor: pointer;
    color: white;
}

.topbarIcons{
    display: flex;
}

.topbarIconItem{
    margin-right: 15px;
    cursor: pointer;
    position: relative;
    color: white;
}

.topbarIconBadge {
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    color: white;
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    align-items:center;
    justify-content: center;
    font-size: 12px;
}

.topbarImage{
    width: 32px;
    height: 32px;
   border-radius: 50%;
   object-fit: cover;
   cursor: pointer;

}

.logo{
    font-size: 24px;
    margin-left: 20px;
    font-weight: bold;
    color: white;
    cursor: pointer;
}