.sidebar{
    flex: 3;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    position: sticky;
    top: 50px;
   
   
}

::-webkit-scrollbar {
width: 10px;
}

::-webkit-scrollbar-track{
    background-color: rgb(12, 12, 19)
}

::-webkit-scrollbar-thumb{
    background-color: red;
}

.sidebarWrapper{
    padding: 20px;
}

.sidebarList{
padding: 0;
margin: 0;
list-style: none;
}

.sidebarListitem{
display: flex;
align-items: center;
margin-bottom: 20px;

}

.sidebarIcon{
margin-right: 15px;
}

.sidebarButton{
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 500;
}

.sidebarHr{
    margin: 20px 0;
}

